.NavContainer {
    display:flex;
    flex-wrap: wrap;
    height:auto;
    padding:0 15px;
    clear:both;
    /* background-color: pink; */
}

.NavLogo {
    display:flex;
    align-items: center;
}

#logo {
    height:20px;
    margin:10px 30px;
}

.NavBar {
    height:63px;
    /* background-color: aqua; */
}

.NavTab {
    display:inline-block;
    box-sizing:content-box;
    height:55px;
    padding:4px 30px;
    border-radius: 0;
    border:none;
    background-color: inherit;
    font-size:16px;
    cursor:pointer;
    transition:all 150ms;
}

.NavTab:hover {
    /* background-color: #b8f2ff; */
    border-bottom:3px solid #15c3ff;
}

.NavTab.Mui-selected  {
    border-bottom:3px solid #15c3ff;
}

.NavTab .settings {
    vertical-align: bottom;
    font-size:16px;
}

.Panels{
    flex:100%;
    padding:15px;
    margin-top:15px;
    /* background-color: blue; */
}