.OptionsContainer {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index:5;
    border-bottom:1px solid lightgray;
    /* overflow-x:hidden; */
    overflow-y: visible;
    white-space: nowrap;
    box-shadow:0 0 5px rgba(22, 22, 22, 0.2);
    background-color:#f3f2f1;
}

.OptionsBar {
    display:flex;
    padding:0;
    margin:0;
    background-color: #323a47;
    height:30px;
    overflow:hidden;
}

.OptionsBtn {
    display: flex;
    justify-content:center;
    align-items: center;
    padding:6px 15px;
    background-color:#323a47;
    cursor: pointer;
    height:30px;
    color:#e8e8e8;
    border:none;
    font-size:inherit;
    font-weight:600;
}
.OptionsBtn.Mui-selected {
    color:black;
    background-color:#f3f2f1;
}
.OptionsBtn.Mui-selected:hover {
    background-color:#f3f2f1;
}

.OptionsBtn:hover {
    background-color:#707D90;
}

.OptionsPanel {
    height:auto;
}

.PanelButtons {
    display:flex;
    flex-flow:row nowrap;
}

.PanelButton {
    vertical-align: middle;
    line-height: 0;
    padding:0 10px;
    height:40px;
    border:none;
    background-color: inherit;
    cursor:pointer;
    color:#212529;
    font-size:inherit;
}

.PanelButton:hover {
    background-color:#e6e6e6;
}

#saveIndicator {
    margin-left: auto;
    display:flex;
    align-items: center;
    padding:0 15px;
    color:#e8e8e8;
}

#saveLight {
    background-color: #66ff99;
    background-color: #ff6666;
    background-color: #FFF77A;
    height: 8px;
    width: 8px;
    border-radius: 50%;
}