.CodeDraggable {
    position:fixed;
    z-index:5;
}

.CodeResizable {
    box-sizing: content-box;
    background-color: #1e1e1e;
    box-shadow:0px 2px 4px black;
}

.CodeHandle {
    box-sizing:border-box;
    display:flex;
    align-items: center;
    justify-content:space-between;
    height:30px;
    width:100%;
    padding:0 20px;
    /* background-color: #2a2f37; */
    background-color: #323a47;
    color:white;
    cursor:grab;
}

.CodeHandle:active {
    cursor:grabbing;
}

/* overrides 'react-resizable/css/styles.css' */
.react-resizable-handle {
    background-image:url("../assets/ResizeHandle.svg");
}